import { createEntityAdapter } from '@reduxjs/toolkit';
import { wordPressApiResourcePath } from '../../const';
import { transformPracticePartner } from '../../utils/api';
import { wordPressApi } from './word-press-api';

const practicePartnersAdapter = createEntityAdapter();

export const practicePartnersSlice = wordPressApi.injectEndpoints({
  endpoints: (builder) => ({
    getPracticePartners: builder.query({
      query: () => wordPressApiResourcePath.PRACTICE_PARTNERS,
      transformResponse: (response) => {
        response = response.map(transformPracticePartner);
        return practicePartnersAdapter.addMany(practicePartnersAdapter.getInitialState(), response);
      },
    }),
  }),
});

export const { useGetPracticePartnersQuery } = practicePartnersSlice;
