/* eslint-disable */
import React from 'react';
import PropTypes from 'prop-types';

export default function PopUpCategory({visible, setVisible, categories}) {
  let html = document.getElementsByTagName('html')[0];
  visible && html.classList.add("fixed");

  return (
    categories &&
    <section className={visible ? 'popup-category' : 'popup-category popup-category--hidden'} onClick={() => {setVisible(false); html.classList.remove("fixed");}}>
      <div className="popup-category__block" onClick={(e) => e.stopPropagation() }>
        <h2 className="popup-category__title">Выберите темы, которые вам интересны</h2>
        <button className="popup-category__close" type="button" onClick={() => {setVisible(false); html.classList.remove("fixed");}} />
        <ul className="block-filter__list">
          {/*popup-category__list */}
          {categories.map((category, i) => {
            return (
              <li key={category.id} className="block-filter__item">
                <input className="block-filter__input visually-hidden" id={category.slug} type="checkbox" name="category" value={category.slug} />
                <label className="block-filter__label" htmlFor={category.slug}>
                  <picture>
                    <source media="(max-width: 768px)" srcSet={category.picture_svg ? category.picture_svg : '/img/courses-page/category-placeholder-mobile.jpg'} />
                    <img className="block-filter__img" src={category.picture_png ? category.picture_png : '/img/courses-page/category-placeholder.jpg'} width="99px" height="99px" alt="" />
                  </picture>
                </label>
                <span className="block-filter__name">{category.title}</span>
              </li>
            );
          })}
        </ul>
      </div>
    </section>
  );
}

PopUpCategory.propTypes = {
  visible: PropTypes.bool.isRequired,
  setVisible: PropTypes.func.isRequired,
  categories: PropTypes.array.isRequired,
};
