import React from 'react';
import ContentLoader from 'react-content-loader';

const DEFAULT_SLIDES_COUNT = 5;
const IMG_WIDTH_IN_PX = 190;
const IMG_HEIGHT_IN_PX = 190;
const SLIDES_HORIZONTAL_GAP_IN_PX = 27;
const SLIDES_START_X_IN_PX = 0;
const SLIDES_START_Y_IN_PX = 0;
const TEXT_LINE_HEIGHT_IN_PX = 20;
const FIRST_TEXT_LINE_Y_IN_PX = 200;
const SECOND_TEXT_LINE_Y_IN_PX = 230;
const BORDER_RADIUS_IN_PX = 20;

function CategoryListLoader(props) {
  const slides = Array(DEFAULT_SLIDES_COUNT).fill(null);

  return (
    <ContentLoader
      speed={1}
      width={1109}
      height={260}
      viewBox="0 0 1109 260"
      backgroundColor="#f3f3f3"
      foregroundColor="#ecebeb"
      {...props}
    >
      {slides.map(( _, index) => {
        const startXInPx = SLIDES_START_X_IN_PX + (IMG_WIDTH_IN_PX + SLIDES_HORIZONTAL_GAP_IN_PX) * index;

        return (
          <React.Fragment key={`slide_${startXInPx}_${SLIDES_START_Y_IN_PX}`}>
            <rect
              x={startXInPx}
              y={SLIDES_START_Y_IN_PX}
              rx={BORDER_RADIUS_IN_PX}
              ry={BORDER_RADIUS_IN_PX}
              width={IMG_WIDTH_IN_PX}
              height={IMG_HEIGHT_IN_PX}
            />
            <rect
              x={startXInPx}
              y={FIRST_TEXT_LINE_Y_IN_PX}
              rx="0"
              ry="0"
              width={IMG_WIDTH_IN_PX}
              height={TEXT_LINE_HEIGHT_IN_PX}
            />
            <rect
              // Second text line must be centered
              x={startXInPx + IMG_WIDTH_IN_PX / 4}
              y={SECOND_TEXT_LINE_Y_IN_PX}
              rx="0"
              ry="0"
              width={IMG_WIDTH_IN_PX / 2}
              height={TEXT_LINE_HEIGHT_IN_PX}
            />
          </React.Fragment>
        );
      })}
    </ContentLoader>
  );
}

export default CategoryListLoader;
