import React from 'react';
import ContentLoader from 'react-content-loader';
import PropTypes from 'prop-types';
import styles from './offers-list-loader.module.css';
import classNames from 'classnames';
import { Breakpoint } from '../../const';

const DEFAULT_WIDTH_IN_PX = Breakpoint.LARGE;
const DEFAULY_HEIGHT_IN_PX = 656;
const DEFAULT_SLIDES_COUNT = 4;
const SLIDE_WIDTH_IN_PX = 325;
const SLIDE_HEIGHT_IN_PX = 528;
const SLIDES_HORIZONTAL_GAP_IN_PX = 21;
const SLIDES_START_X_IN_PX = 40;
const SLIDES_START_Y_IN_PX = 178;

function OffersListLoader({
  slidesCount,
  isWrapper,
  width,
  height,
  ...props}) {
  const slides = Array(slidesCount).fill(null);

  return (
    <ContentLoader
      speed={1}
      width={width}
      height={height}
      viewBox={`0 0 ${width} ${height}`}
      backgroundColor="#f3f3f3"
      foregroundColor="#ecebeb"
      className={classNames({ [styles.Wrapper]: isWrapper })}
      {...props}
    >
      <rect x="0" y="70" rx="0" ry="0" width="300" height="38" />
      {slides.map(( _, index) => {
        const startXInPx = SLIDES_START_X_IN_PX + (SLIDE_WIDTH_IN_PX + SLIDES_HORIZONTAL_GAP_IN_PX) * index;

        return (
          <rect
            key={`slide_${startXInPx}_${SLIDES_START_Y_IN_PX}`}
            x={startXInPx}
            y={SLIDES_START_Y_IN_PX}
            rx="0"
            ry="0"
            width={SLIDE_WIDTH_IN_PX}
            height={SLIDE_HEIGHT_IN_PX}
          />);
      })}
    </ContentLoader>
  );
}

OffersListLoader.propTypes = {
  slidesCount: PropTypes.number.isRequired,
  isWrapper: PropTypes.bool.isRequired,
  width: PropTypes.number.isRequired,
  height: PropTypes.number.isRequired,
};

OffersListLoader.defaultProps = {
  slidesCount: DEFAULT_SLIDES_COUNT,
  isWrapper: false,
  width: DEFAULT_WIDTH_IN_PX,
  height: DEFAULY_HEIGHT_IN_PX,
};

export default OffersListLoader;
