import PropTypes from 'prop-types';
import React from 'react';

export function PageHeader({headerPic, sloganImg, visibleButtonAdd = true}) {
  return (
    <header className="page-header">
      <div className="page-header__plug-img">
        {visibleButtonAdd &&
        <button className="page-header__add-list">
          <svg className="page-header__list-img" xmlns="http://www.w3.org/2000/svg" width="34.859" height={23} viewBox="0 0 34.859 23" alt="">
            <g id="Group_8966" data-name="Group 8966" transform="translate(-1554 -217)">
              <line id="Line_164" data-name="Line 164" x1="31.859" transform="translate(1555.5 218.5)" fill="none" stroke="#24272d" strokeLinecap="round" strokeWidth={3} />
              <line id="Line_165" data-name="Line 165" x1="15.93" transform="translate(1555.5 228.5)" fill="none" stroke="#24272d" strokeLinecap="round" strokeWidth={3} />
              <g id="Group_8961" data-name="Group 8961" transform="translate(1576.615 228.5)">
                <line id="Line_167" data-name="Line 167" x1="10.744" transform="translate(0 5)" fill="none" stroke="#24272d" strokeLinecap="round" strokeWidth={3} />
                <line id="Line_168" data-name="Line 168" x1={10} transform="translate(5.372 0) rotate(90)" fill="none" stroke="#24272d" strokeLinecap="round" strokeWidth={3} />
              </g>
              <line id="Line_166" data-name="Line 166" x1="12.39" transform="translate(1555.5 238.5)" fill="none" stroke="#24272d" strokeLinecap="round" strokeWidth={3} />
            </g>
          </svg>
        </button>}
        <img src={sloganImg} alt="Начни учиться в Sokrart." className="page-header__logo" />
        <img src={headerPic} alt="" className="page-header__course-img" />
      </div>
    </header>
  );
}

PageHeader.propTypes = {
  headerPic: PropTypes.string.isRequired,
  sloganImg: PropTypes.string.isRequired,
  visibleButtonAdd: PropTypes.bool.isRequired,
};

