/* eslint-disable */
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { category } from '../../types/prop-types';
import CategoryListLoader from '../category-list-loader/category-list-loader';
import SwiperCore, { Virtual } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper.min.css';
import { AppRoute } from '../../const';
import { generateAppRoute } from '../../utils/util';
import styles from './category-list.module.css';
import PopUpCategory from '../popup-category/popup-category';

const SLIDES_PER_VIEW = 6;
const IMG_WIDTH_IN_PX = 193;
const IMG_HEIGHT_IN_PX = 193;

SwiperCore.use([Virtual]);

// TODO Error state
export function CategoryList({ categories, isLoading }) {
  // if(!categories) return null;

  // const { ids, entities } = categories;
  // if (!ids.length && !isLoading) {return null;}
  // console.log(entities);
  let content = null;

  const [ visibleAllCategory, setVisibleAllCategory ] = useState(false);

  const reInitSwiper = (swiper) => setTimeout(() => swiper.update());

  if (!categories) {
    content = <CategoryListLoader />;
  } else {
    content = (
      <Swiper
        slidesPerView='auto' //{SLIDES_PER_VIEW}
        // spaceBetween={22}
        // freeMode
        // virtual={{
        //   cache: true,
        //   addSlidesAfter: SLIDES_PER_VIEW,
        //   addSlidesBefore: SLIDES_PER_VIEW,
        // }}

        autoplay={{
          delay: 10000,
        }}
        // Reinitialise swiper right after first initialisation.
        // Otherwise virtual mode will ignore slidesPerView.
        onInit={reInitSwiper}
        breakpoints={{
          768: {
            spaceBetween: 22,
          },
          0: {
            spaceBetween: 10,
          },          
        }}        
        className=''
      >
        {/*{ids.map((id, arrayIndex) => {
          const routePath = generateAppRoute(AppRoute.COURSES_BY_CATEGORY, entities[id].title);
          // console.log(`https://sokratapp.ru/wp-json/wp/v2/media/${entities[id].pictogram}`);

          return (
            <SwiperSlide key={id} virtualIndex={arrayIndex}>
              <div className='category__item' key={id}>
                <Link to={routePath} className='category__img-link'>
                  <img
                    src={entities[id].pictogram}
                    alt={entities[id].title}
                    className={`category__img ${styles.CategoryImg}`}
                    width={IMG_WIDTH_IN_PX}
                    height={IMG_HEIGHT_IN_PX}
                  />
                </Link>
                <p className='category__item-title'>
                  <Link to='#' className='category__title-link'>
                    {entities[id].title}
                  </Link>
                </p>
              </div>
            </SwiperSlide>);
        })}*/}
        {categories.map((category, arrayIndex) => {
          const routePath = generateAppRoute(AppRoute.COURSES_BY_CATEGORY, category.slug);
          return (
            <SwiperSlide key={category.id} virtualIndex={arrayIndex} >
              <div key={category.id} className="block-filter__item--user-categories">
                <input className="block-filter__input visually-hidden"
                // id={category.slug}
                type="checkbox" name="category" value={category.slug} />
                <label className="block-filter__label block-filter__label--user-categories" htmlFor={category.slug}>
                  <picture>
                    <source media="(max-width: 768px)" srcSet="/img/courses-page/category-placeholder-mobile.jpg" />
                    <img className="block-filter__img" src={category.picture_png ? category.picture_png : '/img/courses-page/category-placeholder.jpg'} width="99px" height="99px" alt="" />
                  </picture>
                </label>
                <span className="block-filter__name block-filter__name--user-categories">{category.title}</span>
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
    );
  }

  return (
    <>
      <section className='block-filter block-filter--user-categories'>
        <div className='block-filter__wrapper'>
          <div className='block-filter__header'>
            <h2 className='block-filter__title block-filter__title--user-categories'>Мои категории</h2>
            <div className='block-filter__all-link cursor-pointer' onClick={() => setVisibleAllCategory(true)}>
              Посмотреть все
            </div>
          </div>
          {content}
        </div>
      </section>
      <PopUpCategory
        visible={visibleAllCategory}
        setVisible={setVisibleAllCategory}
        categories={categories}
      />
    </>
  );
}

CategoryList.propTypes = {
  categories: PropTypes
    .shape({
      ids: PropTypes.arrayOf(PropTypes.number).isRequired,
      entities: PropTypes.shape({
        [PropTypes.string]: category,
      }).isRequired,
    })
    .isRequired,
  isLoading: PropTypes.bool.isRequired,
};

CategoryList.defaultProps = {
  categories: { ids: [], entities: {}},
};
