/* eslint-disable */
import React, { useEffect, useCallback, useState } from 'react';
import { fetchData } from '../../store/api/fetch-custom';
import { wordPressApiResourcePath } from '../../const.jsx';

import { MainSlider } from '../main-slider/main-slider';
// import { PopularOffers } from "../popular-offers/popular-offers";
import { CategoryList } from '../category-list/category-list';
import { MobileAppInfo } from '../mobile-app-info/mobile-app-info';
import OffersList from '../offers-list/offers-list';
import { Partners } from '../partners/partners';
import { selectNonEmptyMainCategories, useGetMainCategoriesQuery } from '../../store/api/main-categories-slice';
import { useSelector } from 'react-redux';
import { useGetPartnersQuery } from '../../store/api/partners-slice';
import { useGetPracticePartnersQuery } from '../../store/api/practice-partners-slice';
import { useGetCategoriesQuery } from '../../store/api/categories-slice';
import { nanoid } from 'nanoid';
// import styles from './main-page.module.css';
// import { useGetCoursesQuery } from '../../store/api/courses-slice';
import OffersListLoader from '../offers-list-loader/offers-list-loader';
import MainPageSidebar from '../main-page-sidebar/main-page-sidebar';
import { useGetCampaignsQuery } from '../../store/api/campaigns-slice';
import Message from '../message/message';
import PageLayout from '../page-layout/page-layout';

const SLOTS_NUMBER = 5;
// Multiple instances of Swiper slider on the page requires
// unique identification of the control elements(slider buttons).
// Based on the unique components ids we can generate unique classnames
// and then identify control elements with class selectors.
const PARTNERS_COMPONENT_ID = nanoid();
const PRACTICE_PARTNERS_COMPONENT_ID  = nanoid();

const FIRST_MAIN_COMPONENT_WIDTH = 1440;
const FIRST_MAIN_COMPONENT_SLIDES_PER_VIEW = 3;

export function MainPage() {
  // console.clear();

  // const {
  //   data: campaigns,
  //   isFetching: isCampaignsLoading,
  //   isError: isCampaignsError,
  //   refetch: refetchCampaigns,
  // } = useGetCampaignsQuery();
  // console.log(campaigns);

  // const {
  //   data: mainCategories,
  //   isFetching: isMainCategoriesLoading,
  //   isError: isMainCategoriesError,
  //   refetch: refetchMainCategories,
  // } = useGetMainCategoriesQuery();
  // console.log(mainCategories);

  // const {
  //   data: categories,
  //   isFetching: isCategoriesLoading,
  //   isError: isCategoriesError,
  //   refetch: refetchCategories,
  // } = useGetCategoriesQuery();
  // console.log(categories);

  // const {
  //   isFetching: isCoursesLoading,
  //   isError: isCoursesError,
  //   refetch: refetchCourses,
  // } = useGetCoursesQuery();

  // const {
  //   data: partners,
  //   isFetching: isPartnersLoading,
  //   isError: isPartnersError,
  //   refetch: refetchPartners,
  // } = useGetPartnersQuery();
  // console.log(partners);
  // const {
  //   data: practicePartners,
  //   isFetching: isPracticePartnersLoading,
  //   isError: isPracticePartnersError,
  //   refetch: refetchPracticePartners,
  // } = useGetPracticePartnersQuery();

  const [ campaigns, setCampaigns ] = useState('');
  const [ categories, setCategories ] = useState('');
  const [ mainCategories, setMainCategories ] = useState('');
  const [ partners, setPartners ] = useState('');
  const [ practicePartners, setPracticePartners ] = useState('');
  const [ totalData, setTotalData ] = useState('');

  useEffect(async () => {
    !totalData &&
    setTotalData(await fetchData({
      mainCategories: wordPressApiResourcePath.MAIN_CATEGORIES,
      categories: wordPressApiResourcePath.CATEGORIES,
      campaigns: wordPressApiResourcePath.CAMPAIGNS,
      partners: wordPressApiResourcePath.PARTNERS_MAIN,
      practicePartners: wordPressApiResourcePath.PARTNERS_PRACTICE_MAIN,
    }));
    // if(totalData) console.log(totalData);
    if(totalData && !totalData.error) {
      setMainCategories(totalData.mainCategories.data);
      setCategories(totalData.categories.data);
      setCampaigns(totalData.campaigns.data);
      setPartners(totalData.partners.data);
      setPracticePartners(totalData.practicePartners.data);
    };
    // await fetchData('ПРОДУКТ', 'product/3881');  
  });
  // console.log(totalData);
  // let campaigns, isCampaignsLoading, isCampaignsError, refetchCampaigns;
  // console.log('рендер!');

  // const mainCategories = useSelector(selectNonEmptyMainCategories);

  const refetchAllEndpoints = useCallback(() => {
    // refetchMainCategories();
    // refetchCourses();
    // refetchPartners();
    // refetchPracticePartners();
    // refetchCategories();
    // refetchCampaigns();
    setTotalData('');
  }, [
    // refetchMainCategories,
    // refetchCourses,
    // refetchPartners,
    // refetchPracticePartners,
    // refetchCategories,
    // refetchCampaigns,
    setTotalData,
  ]);

  // let isFetchingError = isMainCategoriesError;
  //   // || isCoursesError
  //   // || isPartnersError
  //   // || isPracticePartnersError
  //   // || isCategoriesError
  //   // || isCampaignsError;
  
  let isLoading = false;
  let isFetchingError = false;
  let content = null;

  // console.log(isFetchingError);
  if (totalData.error) {
    //  || !mainCategories
    content = <Message onButtonClick={refetchAllEndpoints} />;
  // } else if(!mainCategories) {
  //   content = <Message onButtonClick={refetchAllEndpoints} />;
  } else {
    // Set global loading state on the page.
    // Do not show any components content until all other components
    // will be loaded.
    // const isLoading =  isMainCategoriesLoading
    //   // || isCoursesLoading
    //   // || isPartnersLoading
    //   // || isPracticePartnersLoading
    //   || isCategoriesLoading
    //   || isCampaignsLoading;

    let mainCategoryComponents;
    if(mainCategories) {
      // console.log(mainCategories);
      mainCategoryComponents = mainCategories.map(({ id, title, main_courses }, index) => {
        let sliderWidthAuto = false;
        let slidesPerView = 4;
        let navigation=true;
        // Customize first slider
        if (index === 0) {
          // Set first component custom slides per view value
          slidesPerView = FIRST_MAIN_COMPONENT_SLIDES_PER_VIEW;
          // navigation=false;
          //Fix css width because of the lack of the wrapper
          // html elements or css classes!
          sliderWidthAuto = true;
        }

        return (
          <OffersList
            key={id}
            title={title}
            // offersIds={coursesIds}
            mainCourses={main_courses}
            componentId={nanoid()}
            slidesPerView={slidesPerView}
            navigation={navigation}
            sliderWidthAuto={sliderWidthAuto}
          />);
      });
    }


    content = (
      <>
        <MainSlider campaigns={campaigns} isLoading={true} />
        <div className='container block-sb-right'>
          <div className='block-sb-right__wrapper'>
            <div className='block-sb-right__content'>
              <div className='block-sb-right__carousel'>
                {!mainCategories
                  ? (
                    <OffersListLoader
                      slidesCount={3}
                      width={FIRST_MAIN_COMPONENT_WIDTH}
                    />)
                  : mainCategoryComponents[0]}
              </div>

              <div className='block-sb-right__filter'>
                <CategoryList
                  categories={categories}
                  isLoading={true}
                />
              </div>
            </div>
            <MainPageSidebar />
          </div>
        </div>
        <MobileAppInfo />

        <div className='container'>
          {!mainCategories
            ? <OffersListLoader isWrapper />
            : mainCategoryComponents[1]}
          {!mainCategories
            ? <OffersListLoader isWrapper />
            : mainCategoryComponents[2]}

          {!partners
            ? <Partners
              title='Партнеры'
              isLoading={true}
              componentId={PARTNERS_COMPONENT_ID}
            />
            : <Partners
              title='Партнеры'
              partners={partners[0].partners_list}
              isLoading={false}
              componentId={PARTNERS_COMPONENT_ID}
            />
          }

          {!mainCategories
            ? <OffersListLoader isWrapper />
            : mainCategoryComponents[3]}
          {!mainCategories
            ? <OffersListLoader isWrapper />
            : mainCategoryComponents[4]}

          {!practicePartners
            ? <Partners
              title='Пройди у них практику'
              isLoading={true}
              componentId={PRACTICE_PARTNERS_COMPONENT_ID}
            />
            : <Partners
              title='Пройди у них практику'
              partners={practicePartners[0].partners_list}
              isLoading={false}
              componentId={PRACTICE_PARTNERS_COMPONENT_ID}
            />
          }

        {/*{isLoading || mainCategoryComponents.slice(SLOTS_NUMBER)}*/}
        </div>
      </>
    );

  }

  // content = '123';

  return (
    <PageLayout>
      {content}
    </PageLayout>
  );
}