/* eslint-disable */
import PropTypes from 'prop-types';
import React from 'react';
import NumberFormat from 'react-number-format';
import { nanoid } from 'nanoid';
import ContentLoader from 'react-content-loader';


export function CoursesList({courses, isLoading}) {
  let contentCats;

  // loader
  for(let count=0;  count < 6; count++) {
    contentCats = (
      <>
        {contentCats}
        <ContentLoader 
          // speed={2}
          width={307}
          height={317}
          // viewBox="0 0 400 160"
          backgroundColor="#f3f3f3"
          foregroundColor="#ecebeb"
        >
          <rect x="0" y="0" rx="10" ry="10" width="307" height="317" />
        </ContentLoader>
      </>
    )
  }

  if(!isLoading) {
    contentCats = '';
    const { entities } = courses;
    // console.log(entities);
    // const reversedEntities = entities.reverse()
    for (const id in entities) {
      const { title, slug, duration, price, typecount, link, illustration } = entities[id];
      contentCats = (
        <>
          {contentCats}
          <li key={id} className="block-courses__item">
            {/*<img className="instructor__courses-img" src={trumb} width={282} height={151} alt="" />*/}
            <img className="block-courses__img" src={illustration ? illustration : '/img/courses-page/course_default-trumb.png'} width={282} height={151} alt="" />
            <p className="block-courses__price">
              <NumberFormat value={entities[id].course_price} displayType={'text'} thousandSeparator=" " suffix={` ${entities[id].type_of_pay}`} />
              <span className="block-courses__term">
                {duration}
              </span>
            </p>
            <p className="block-courses__title">{title}</p>
            <a className="block-courses__link btn" href={'/courses/' + slug} data-link={link} target="_blank" rel="noreferrer">Подробнее</a>
          </li>
        </>
      );
    }

  }

  return (
    <>
      <ul className="block-courses">
        {contentCats}
      </ul>
      <button class="block-courses__btn btn">Показать еще</button>
    </>
  );
}

CoursesList.propTypes = {
  // title: PropTypes.string.isRequired,
  courses: PropTypes
    .shape({
      ids: PropTypes.arrayOf(PropTypes.number).isRequired,
      // entities: PropTypes.Array.isRequired,
      entities: PropTypes.shape({
        title: PropTypes.string,
        // [PropTypes.string]: title,
      }).isRequired,
    })
    .isRequired,
  isLoading: PropTypes.bool.isRequired,
  // componentId: PropTypes.string.isRequired,
};

CoursesList.defaultProps = {
  courses: { ids: [], entities: {}},
};

