/* eslint-disable */
import PropTypes from 'prop-types';
import React from 'react';
import NumberFormat from 'react-number-format';

export function CourseInfo({database, isLoading}) {
  let html;
  if(database) {
    html = (
      <div className="course__info">
        <h1 className="course__title">{database.title.rendered}</h1>
        <p className="course__price">Стоимость обучения <NumberFormat value={database.course_price} displayType={'text'} thousandSeparator=" " suffix={database.type_of_pay} /></p>
        <p className="course__description">{database.content}</p>
        <button class="course__button-more">Подробнее</button>
      </div>
    )
  }

  return (
    <>{html}</>
  );

}

CourseInfo.propTypes = {
  database: PropTypes.array.isRequired,
  isLoading: PropTypes.bool.isRequired,
};

