/* eslint-disable */
import React, { useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Pagination, Autoplay } from 'swiper';
import PropTypes from 'prop-types';
import MainSliderLoader from '../main-slider-loader/main-slider-loader';
import 'swiper/swiper.min.css';
import 'swiper/components/pagination/pagination.min.css';
// import './main-slider.css';
import { campaign } from '../../types/prop-types';

const SPACE_BETWEEN_SLIDES_IN_PX = 20;
const AUTOPLAY_DELAY_IN_MS = 5000;
const IMG_WIDTH_IN_PX = 700;
const IMG_HEIGHT_IN_PX = 450;

SwiperCore.use([Pagination, Autoplay]);

export function MainSlider({ campaigns, isLoading }) {

  const [visiblePromoPopUp, setVisiblePromoPopUp] = useState(false);
  const [contentPromoPopUp, setContentPromoPopUp] = useState(null);
  let content;

  if (!campaigns) {
    content = <MainSliderLoader />;
  } else {
    content = (
      <>
        <Swiper
          className='main-page-swiper__swiper'
          slidesPerView='auto'
          spaceBetween={SPACE_BETWEEN_SLIDES_IN_PX}
          pagination={{
            el: '.main-page-swiper__pagination',
            clickable: true,
          }}
          autoplay={{
            delay: AUTOPLAY_DELAY_IN_MS,
          }}
        >
          {campaigns.map((contentPromo) => (
            <SwiperSlide className='main-page-swiper__swiper-wrapper' key={contentPromo.id}>
              <div
              className="main-page-swiper__swiper-slide"
              style={{
                'background-image': `url("${contentPromo.picture_promo}")`,
              }}
              onClick={() => {
                  setContentPromoPopUp(contentPromo);
                  setVisiblePromoPopUp(true);
                }}
              />
              {/*<div className='main-slider__link'>
                <img
                  className='main-slider__img'
                  src={contentPromo.picture_promo}
                  alt=''
                  width={IMG_WIDTH_IN_PX}
                  height={IMG_HEIGHT_IN_PX}
                  onClick={() => {
                    setContentPromoPopUp(contentPromo);
                    setVisiblePromoPopUp(true);
                  }}
                />
              </div>*/}
            </SwiperSlide>
          ))}
        </Swiper>

        <div className='main-page-swiper__pagination swiper-pagination' />
      </>
    );
  }

  return (
    <section className='main-page-swiper'>
      <div className='main-page-swiper__wrapper'>
        {/*<h1 className='visually-hidden'>Sokrat — Тысячи знаний внутри</h1>*/}
        {content}
        <MainSliderPopUp
          visible={visiblePromoPopUp}
          setVisible={setVisiblePromoPopUp}
          contentPromo={contentPromoPopUp}
        />
      </div>
    </section>
  );
}

function MainSliderPopUp({visible, setVisible, contentPromo}) {
  let html = document.getElementsByTagName('html')[0];
  visible && html.classList.add("fixed");

  return (
    <section className={visible ? 'popup-promotion' : 'popup-promotion popup-promotion--hidden'} onClick={() => {setVisible(false); html.classList.remove('fixed'); }}>
      <div className="popup-promotion__block" onClick={(e) => e.stopPropagation()}>
        <button className="popup-promotion__close" type="button" onClick={() => {setVisible(false); html.classList.remove('fixed'); }}/>
        {visible &&
          <>
            <img className="popup-promotion__img" src={contentPromo.picture_promo} alt="" />
            <div className="popup-promotion__info">
              <p className="popup-promotion__slogan">{contentPromo.title}</p>
              <p className="popup-promotion__text">{contentPromo.content}</p>
              <p className="popup-promotion__title">Партнер акции</p>
              <div className="popup-promotion__partner">
                <div className="popup-promotion__partner__img">
                  <img src={contentPromo.sokrat_learn_partner.learn_partner_png} alt="" />
                </div>
                {contentPromo.sokrat_learn_partner.post_title}
              </div>
              <p className="popup-promotion__title">Купон</p>
              <div className="popup-promotion__code">{contentPromo.coupon_promo}</div>
              <a className="popup-promotion__button btn" href={contentPromo.link_promolink_promo} target="_blank" rel="noopener noreferrer">Перейти</a>
            </div>
          </>}
      </div>
    </section>
  );
}

MainSlider.propTypes = {
  campaigns: PropTypes.arrayOf(campaign),
  isLoading: PropTypes.bool.isRequired,
};

MainSlider.defaultProps = {
  campaigns: [],
};

MainSliderPopUp.propTypes = {
  visible: PropTypes.bool.isRequired,
  setVisible: PropTypes.func.isRequired,
  contentPromo: PropTypes.array.isRequired,
};
