/* eslint-disable */
import PropTypes from 'prop-types';
import React from 'react';
import { nanoid } from 'nanoid';
import ContentLoader from 'react-content-loader';

export function PartnerInfo({database, isLoading}) {
  console.log(database);
  const SquareLoader = () => (
      <ContentLoader className='loader-square'>
        <rect x="0" y="0" rx="10" ry="10" width="373" height="373" />
      </ContentLoader>
    );
  
  const ButtonLoader = () => (
      <ContentLoader className='loader-button'>
        <rect x="0" y="0" rx="10" ry="10" width="373" height="45" />
      </ContentLoader>
    );

  const DescrLoader = () => (
      <ContentLoader className='loader-descr'>
        <rect x="0" y="0" rx="10" ry="10" width="373" height="20" />
      </ContentLoader>
    );

  const content = (
    <div className="instructor__info">
      <div className="instructor__avatar">
        {isLoading ?
          <SquareLoader /> :
          <img src={database.logo_png.guid} width="373px" height="373px" alt="" />}
      </div>
      <div className="instructor__data">
        {isLoading ?
          <>
            <ContentLoader style={{width: '100%', height: '100%'}}>
              <rect x="0" y="0" rx="10" ry="10" width="900" height="64" />
              <rect x="0" y="84" rx="10" ry="10" width="900" height="64" />
              <rect x="0" y="168" rx="10" ry="10" width="900" height="64" />
              <rect x="0" y="252" rx="10" ry="10" width="900" height="64" />
            </ContentLoader>
          </> :
          <>
            <div className="instructor__inner">
              <h2 className="instructor__title">{database.title.rendered}</h2>
              <p className="instructor__text">{database.content.rendered}</p>
              <button className="instructor__more">показать еще</button>
            </div>

            <h3 className="partner__subtitle">Детали</h3>
            <ul className="partner__list">
              <li className="partner__item">
                <a className="partner__link" href="#">
                  Год основания
                  <span className="partner__number">
                    2012
                  </span>
                </a>
              </li>
              <li className="partner__item">
                <a className="partner__link" href="#">
                  Всего курсов
                  <span className="partner__number">
                    1500
                  </span>
                </a>
              </li>
              <li className="partner__item">
                <a className="partner__link" href="#">
                  Рейтинг
                  <span className="partner__number">
                    4.8
                  </span>
                </a>
              </li>
            </ul>

            {/*<h2 className="instructor__title instructor__title--small">Детали</h2>
            <ul className="instructor__list">
              <li className="instructor__item">
                <a className="instructor__link instructor__link--portfolio" href="#">
                  <p className="instructor__information">Текущая должность</p>
                  <p className="instructor__prompt instructor__prompt--active">{database.teacher_position}</p>
                </a>
              </li>
              <li className="instructor__item">
                <a className="instructor__link instructor__link--experience" href="#">
                  <p className="instructor__information">Опыт <span className="instructor__number instructor__number--center">{database.teacher_experience}</span></p>
                  <p className="instructor__prompt">Работал в должности</p>
                </a>
              </li>
              <li className="instructor__item">
                <a className="instructor__link instructor__link--simple" href="#">
                  Рейтинг <span className="instructor__number">{database.teacher_raiting}</span>
                </a>
              </li>
            </ul>*/}
          </>
        }
      </div>
      <div className='instructor__btn'>
        {isLoading ?
          <ButtonLoader /> :
          <a className=" btn" href="#">Оставить отзыв</a>
        }
      </div>
    </div>
  );

  return (
    <>{content}</>
  );
}

PartnerInfo.propTypes = {
  database: PropTypes.array.isRequired,
  isLoading: PropTypes.bool.isRequired,
};
